* {
  margin: 0; }

h3, h4, p {
  opacity: 0.9; }

nav {
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: row;
  position: relative;
  background: #019ed3; }
  nav a {
    text-decoration: none;
    padding: 5px 8px;
    border-radius: 10px;
    color: #ffffff;
    font-weight: 900; }
  nav .leftSide {
    height: 100%;
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 8px; }
    nav .leftSide .iconA {
      height: 100%;
      width: 100%; }
      nav .leftSide .iconA .navName {
        color: #ffffff;
        width: 100%; }
      nav .leftSide .iconA .navPara {
        color: #383838;
        font-weight: 900;
        opacity: 1; }
  nav .rightSide {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    nav .rightSide .navMenuDiv {
      display: flex;
      flex-direction: row;
      width: 150px;
      justify-content: space-evenly; }
      nav .rightSide .navMenuDiv .myStory {
        transition: ease-out all 0.2s; }
        nav .rightSide .navMenuDiv .myStory:hover {
          transition: ease-out all 0.2s;
          box-shadow: inset 1px 1px 5px black; }
  @media (max-width: 650px) {
    nav .leftSide {
      width: 60%; }
    nav .rightSide {
      width: 40%; } }
